                We will try to accommodate as best as possible your preferre
                dates/times
            </p>

            <p>
                <strong>
                    * Please note: request cannot be within 1 bussiness day
                </strong>
            </p>
        </div>
    </div>

    <div class="row">
        <div class="col-8 q-px-xs">
            <q-input
                v-model="prefDate1"
                ref="prefDate1Ref"
                :rules="[
                    'date',
                    (val) => (val && val.length > 0) || 'Please type something',
                ]"
                mask="date"
                label="Preferred Date #1"
                outlined
            >
                <template v-slot:append>
                    <q-icon name="event" class="cursor-pointer">
                        <q-popup-proxy
                            ref="prefDate1Proxy"
                            cover
                            transition-show="scale"
                            transition-hide="scale"
                        >
                            <q-date
                                v-model="prefDate1"
                                @update:model-value="
                                    closeDatePicker('prefDate1Proxy')
                                "
                                :options="optionsFn"
                            >
                                <div class="row items-center justify-end">
                                    <q-btn
                                        v-close-popup
                                        label="Close"
                                        color="primary"
                                        flat
                                    />
                                </div>
                            </q-date>
                        </q-popup-proxy>
                    </q-icon>
                </template>
            </q-input>
        </div>

        <div class="col-4 q-px-xs">
            <q-input
                v-model="prefTime1"
                ref="prefTime1Ref"
                mask="time"
                :rules="[
                    'time',
                    (val) => (val && val.length > 0) || 'Please type something',
                ]"
                label="Time #1"
                outlined
            >
                <template v-slot:append>
                    <q-icon name="access_time" class="cursor-pointer">
                        <q-popup-proxy
                            ref="prefTime1Proxy"
                            cover
                            transition-show="scale"
                            transition-hide="scale"
                        >
                            <q-time
                                v-model="prefTime1"
                                @update:model-value="
                                    closeDatePicker('prefTime1Proxy')
                                "
                                mask="HH:mm"
                            >
                                <div class="row items-center justify-end">
                                    <q-btn
                                        v-close-popup
                                        label="Close"
                                        color="primary"
                                        flat
                                    />
                                </div>
                            </q-time>
                        </q-popup-proxy>
                    </q-icon>
                </template>
            </q-input>
        </div>

        <div class="col-8 q-px-xs">
            <q-input
                v-model="prefDate2"
                ref="prefDate2Ref"
                :rules="[
                    'date',
                    (val) => (val && val.length > 0) || 'Please type something',
                ]"
                mask="date"
                label="Preferred Date #2"
                outlined
            >
                <template v-slot:append>
                    <q-icon name="event" class="cursor-pointer">
                        <q-popup-proxy
                            ref="prefDate2Proxy"
                            cover
                            transition-show="scale"
                            transition-hide="scale"
                        >
                            <q-date
                                v-model="prefDate2"
                                @update:model-value="
                                    closeDatePicker('prefDate2Proxy')
                                "
                                :options="optionsFn"
                                mask="YYYY-MM-DD"
                            >
                                <div class="row items-center justify-end">
                                    <q-btn
                                        v-close-popup
                                        label="Close"
                                        color="primary"
                                        flat
                                    />
                                </div>
                            </q-date>
                        </q-popup-proxy>
                    </q-icon>
                </template>
            </q-input>
        </div>

        <div class="col-4 q-px-xs">
            <q-input
                v-model="prefTime2"
                ref="prefTime2Ref"
                mask="time"
                :rules="[
                    'time',
                    (val) => (val && val.length > 0) || 'Please type something',
                ]"
                label="TIme #2"
                outlined
            >
                <template v-slot:append>
                    <q-icon name="access_time" class="cursor-pointer">
                        <q-popup-proxy
                            ref="prefTime2Proxy"
                            cover
                            transition-show="scale"
                            transition-hide="scale"
                        >
                            <q-time
                                v-model="prefTime2"
                                @update:model-value="
                                    closeDatePicker('prefTime2Proxy')
                                "
                                mask="HH:mm"
                            >
                                <div class="row items-center justify-end">
                                    <q-btn
                                        v-close-popup
                                        label="Close"
                                        color="primary"
                                        flat
                                    />
                                </div>
                            </q-time>
                        </q-popup-proxy>
                    </q-icon>
                </template>
            </q-input>
        </div>

        <div class="col-8 q-px-xs">
            <q-input
                v-model="prefDate3"
                ref="prefDate3Ref"
                :rules="[
                    'date',
                    (val) => (val && val.length > 0) || 'Please type something',
                ]"
                mask="date"
                label="Preferred Date #3"
                outlined
            >
                <template v-slot:append>
                    <q-icon name="event" class="cursor-pointer">
                        <q-popup-proxy
                            ref="prefDate3Proxy"
                            cover
                            transition-show="scale"
                            transition-hide="scale"
                        >
                            <q-date
                                v-model="prefDate3"
                                @update:model-value="
                                    closeDatePicker('prefDate3Proxy')
                                "
                                :options="optionsFn"
                            >
                                <div class="row items-center justify-end">
                                    <q-btn
                                        v-close-popup
                                        label="Close"
                                        color="primary"
                                        flat
                                    />
                                </div>
                            </q-date>
                        </q-popup-proxy>
                    </q-icon>
                </template>
            </q-input>
        </div>

        <div class="col-4 q-px-xs">
            <q-input
                label="Time #3"
                v-model="prefTime3"
                ref="prefTime3Ref"
                mask="time"
                :rules="[
                    'time',
                    (val) => (val && val.length > 0) || 'Please type something',
                ]"
                outlined
            >
                <template v-slot:append>
                    <q-icon name="access_time" class="cursor-pointer">
                        <q-popup-proxy
                            ref="prefTime3Proxy"
                            cover
                            transition-show="scale"
                            transition-hide="scale"
                        >
                            <q-time
                                v-model="prefTime3"
                                @update:model-value="
                                    closeDatePicker('prefTime3Proxy')
                                "
                                mask="HH:mm"
                            >
                                <div class="row items-center justify-end">
                                    <q-btn
                                        v-close-popup
                                        label="Close"
                                        color="primary"
                                        flat
                                    />
                                </div>
                            </q-time>
                        </q-popup-proxy>
                    </q-icon>
