    </div>

    <div class="row">
        <div class="col-12">
            <label>Comment, if any</label>
            <q-input
                v-model="comment"
                ref="commentRef"
                :rules="[
                    (val) => (val && val.length > 0) || 'Please type something',
                ]"
                outlined
                type="textarea"
            />
        </div>
    </div>

    <div class="row q-mt-lg">
        <q-btn
            label="Request Appointment"
            color="indigo"
            size="lg"
            style="width: 100%"
            @click="requestAppointment"
        ></q-btn>
    </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";
import { BASE_PATH } from "@/main";
import { date, useQuasar } from "quasar";
import { TYPE_OF_APPOINTMENTS, PREFERRED_CLINIC } from "@/constants";
import { useRouter } from "vue-router";

export default {
    setup() {
        const typeOfAppointment = ref();
        const typeOfAppointmentOpts = ref(TYPE_OF_APPOINTMENTS);
        const prefferedClinic = ref();
        const prefferedClinicOpts = ref(PREFERRED_CLINIC);

        const prefDate1 = ref();
        const prefDate2 = ref();
        const prefDate3 = ref();
        const prefTime1 = ref();
        const prefTime2 = ref();
        const prefTime3 = ref();
        const comment = ref();

        const typeOfAppointmentRef = ref(null);
        const prefferedClinicRef = ref(null);
        const prefDate1Ref = ref(null);
        const prefDate2Ref = ref(null);
        const prefDate3Ref = ref(null);
        const prefTime1Ref = ref(null);
        const prefTime2Ref = ref(null);
        const prefTime3Ref = ref(null);
        const commentRef = ref(null);

        const closeDatePicker = (value) => {};
        const optionsFn = (_date) => {
            const dateValid = date.addToDate(new Date(), {
                days: 2,
            });
            return (
                _date >
                date.formatDate(
                    date.addToDate(new Date(), {
                        days: 1,
                    }),
                    "YYYY/MM/DD"
                )
            );
        };

        const $q = useQuasar();
        const router = useRouter();

        const requestAppointment = () => {
            typeOfAppointmentRef.value.validate();
            prefferedClinicRef.value.validate();
            prefDate1Ref.value.validate();
            prefDate2Ref.value.validate();
            prefDate3Ref.value.validate();
            prefTime1Ref.value.validate();
            prefTime2Ref.value.validate();
            prefTime3Ref.value.validate();
            commentRef.value.validate();

            if (
                typeOfAppointmentRef.value.hasError ||
                prefferedClinicRef.value.hasError ||
                prefDate1Ref.value.hasError ||
                prefDate2Ref.value.hasError ||
                prefDate3Ref.value.hasError ||
                prefTime1Ref.value.hasError ||
                prefTime2Ref.value.hasError ||
                prefTime3Ref.value.hasError ||
                commentRef.value.hasError
            ) {
                return;
            }

            const user = JSON.parse(localStorage.getItem("USER"));

            const payload = {
                id: user.Id,
                typeOfAppointment: typeOfAppointment.value,
                prefferedClinic: prefferedClinic.value,
                prefDate1: prefDate1.value,
                prefDate2: prefDate2.value,
                prefDate3: prefDate3.value,
                prefTime1: prefTime1.value,
                prefTime2: prefTime2.value,
                prefTime3: prefTime3.value,
                comment: comment.value,
                patientFirstName: user.FirstName,
                patientLastName: user.LastName,
                patientEmail: user.Email,
                patientMobile: user.mobileNumber,
            };

            axios.post(`${BASE_PATH}/request-appointment`, payload).then(() => {
                $q.notify({
                    message: "Request Appointment Successful",
                    icon: "info",
                    position: "top-right",
                    color: "positive",
                });

                router.push({ name: "Home" });
            });
        };

        return {
            typeOfAppointment,
            typeOfAppointmentOpts,
            prefferedClinic,
            prefferedClinicOpts,
            prefDate1,
            prefDate2,
            prefDate3,
            prefTime1,
            prefTime2,
            prefTime3,
            comment,
            typeOfAppointmentRef,
            prefferedClinicRef,
            prefDate1Ref,
            prefDate2Ref,
            prefDate3Ref,
            prefTime1Ref,
            prefTime2Ref,
            prefTime3Ref,
            commentRef,
            requestAppointment,
            closeDatePicker,
            optionsFn,
        };
    },
};
</script>
